import React, { useState, useEffect } from "react";
import "./Gallery.css";
import ImageCard from "../ImageCard/ImageCard";
import GalleryHeader from "../GalleryHeader/GalleryHeader";
import GalleryFooter from "../GalleryFooter/GalleryFooter";

function Gallery(props) {
  const [totalPics, setTotalPics] = useState(100);
  const [noOfPicsPerPage, setnoOfPicsPerPage] = useState(10);
  const [curentHeaderInput, setcurentHeaderInput] = useState(props.curentPage);

  var noOfPages = totalPics / noOfPicsPerPage;
  let isLastPage = props.curentPage === noOfPages;
  let isFirstPage = props.curentPage === 1;

  const handleBtn = (per) => {
    if (per === "Next Page") {
      if (props.curentPage < noOfPages) {
        console.log("next");
        handleNextbtn();
      }
    } else if (per === "Previous") {
      if (props.curentPage > 1) {
        console.log("Previous");
        handlePreviousbtn();
      }
    } else handleSqurebtn(per);
  };

  const handleNextbtn = () => {
    props.curentPageHandler(props.curentPage + 1);
  };

  const handlePreviousbtn = () => {
    props.curentPageHandler(props.curentPage - 1);
  };

  const handleSqurebtn = (per) => {
    console.log("squre");

    props.curentPageHandler(per);
  };
  const handleGInputChange = (e) => {
    setcurentHeaderInput(Number(e.target.value));
  };
  const handleGInputSubmit = (e) => {
    e.preventDefault();

    if (curentHeaderInput > 0 && curentHeaderInput <= noOfPages) {
      props.curentPageHandler(curentHeaderInput);
    }
  };

  return (
    <div className="Gallery">
      <GalleryHeader
        noOfPages={noOfPages}
        handleGInputChange={handleGInputChange}
        handleGInputSubmit={handleGInputSubmit}
        curentHeaderInput={curentHeaderInput}
      />

      <div className="imageWraper slide-in-left">
        
        {props.urls.map((url)=>{
            return(
              <ImageCard sampleImg = {url}/>
            )
        })}
      </div>

      <GalleryFooter
        curentPage={props.curentPage}
        isFirstPage={isFirstPage}
        isLastPage={isLastPage}
        noOfPages={noOfPages}
        handleBtn={handleBtn}
      />
    </div>
  );
}

export default Gallery;
