import React, { useState, useEffect } from "react";
import "./App.css";
import Navbar from "./Components/Navbar/Navbar";
import SearchArea from "./Components/SearchArea/SearchArea";
import Gallery from "./Components/Gallery/Gallery";
import GalleryNavigationBtn from "./Components/GalleryNavigationBtn/GalleryNavigationBtn";
import Footer from "./Components/Footer/Footer";
import axios from "axios";
// import ErrorBoundrayComponent from "./Components/ErrorBoundaryComponent/ErrorBoundaryComponent";
function App() {
  const [searchItem, setSearchItem] = useState("programming");
  const [searchSubmitedItem, setSearchSubmitedItem] = useState("programming");
  const [urls, setUrls] = useState([]);
  const [curentPage, setCurentPage] = useState(1);

  useEffect(() => {
    setUrls([]);
    console.log(`https://api.unsplash.com/search/photos?page=${curentPage}&query=${searchSubmitedItem}"&client_id=R-IYDvvxdtbB7jJzYFeXbRjtaEFRc64w2XqtxPDWtmM`);
    axios
      .get(
        `https://api.unsplash.com/search/photos?page=${curentPage}&query=${searchSubmitedItem}"&client_id=R-IYDvvxdtbB7jJzYFeXbRjtaEFRc64w2XqtxPDWtmM`
      )
      .then((res) => {
        let urlres = [];
        res.data.results.map((per) => urlres.push(per.urls.regular));
        setUrls(urlres);
      });
  }, [searchSubmitedItem,curentPage]);

  const formSubmitHandler = (e) => {
    e.preventDefault();
    setSearchSubmitedItem(searchItem);
  };
  const formChangeHandler = (e) => {
    setSearchItem(e.target.value);
    // console.log(e);
  };
  const curentPageHandler = (per) => {
    setCurentPage(per);
  };

  return (
    <div>
      <Navbar />
      <SearchArea
        searchItem={searchItem}
        formChangeHandler={formChangeHandler}
        formSubmitHandler={formSubmitHandler}
      />
      <Gallery curentPageHandler={curentPageHandler} curentPage={curentPage} urls={urls} />
      <GalleryNavigationBtn />
      <Footer />
    </div>
  );
}

export default App;

